
<template>
    <div>
        <nav class="navbar" role="navigation" aria-label="main navigation">
        <div class="navbar-brand">
            <a class="navbar-item" href="https://bulma.io">
                <img src="https://bulma.io/images/bulma-logo.png" width="112" height="28">
            </a>

            <button  class="button navbar-item m-3 is-link" v-on:click="addMovieShowForm" id="addFormBtn">
               Add Movie
            </button>
        </div>
        </nav>
        <table class="table">
            <thead>
                <tr>
                    <th title="Name">Name</th>
                    <th title="Description">Description</th>
                    <th title="Release Year">Release Year</th>
                    <th title="Actions">Actions</th>
                </tr>
            </thead>
            <tbody>
                <tr v-for="movie in movies" v-bind:key="movie.id">
                    <td>
                        {{movie.name}}
                    </td>
                    <td>
                        {{movie.description}}
                    </td>
                    <td>
                        {{movie.releaseDate}}
                    </td>
                    <td>
                        <button class="btn btn-cancel editMovie"
                                v-on:click="editMovie">
                            Edit
                        </button>
                        <button class="btn btn-cancel copyMovie"
                                v-on:click="copyMovie(movie.id, movie)">
                            Copy
                        </button>
                        <button class="btn btn-cancel deleteMovie"
                                v-on:click="deleteMovie(movie.id)">
                            Delete
                        </button>
                    </td>

                </tr>

            </tbody>

        </table>
        <div class="column is-half is-hidden" id="formContainer">
            <div class="field">
                <label class="label">Name</label>
                <div class="control">
                    <input class="input" maxlength="50" type="text" placeholder="Text input" v-model="movie.name">
                </div>
                <p class="help">Enter movie name here. Limit 50 characters.</p>
            </div>


            <div class="field">
                <label class="label">Description</label>
                <div class="control">
                    <textarea class="textarea" maxlength="500" placeholder="Textarea" v-model="movie.description"></textarea>
                </div>
                <p class="help">Enter movie dscription here. Limit 500 characters.</p>
            </div>

            <div class="field">
                <label class="label">Release Year</label>
                <div class="control">
                    <input class="input" type="number" minlenght="4" maxlength="4" placeholder="Number input" v-model="movie.releaseDate">
                </div>
                <p class="help">Enter four digit movie release year here. Example 1992. </p>
            </div>

            <div class="field is-grouped">
                <div class="control">
                    <button class="button is-link" type="submit"  v-on:click="addMovie()">Submit</button>
                </div>
                <div class="control">
                    <button class="button is-link is-light">Cancel</button>
                </div>
            </div>


        </div>
    </div>
</template>

<script>
    import MovieService from '@/services/MovieService.js'

    export default {
        name: 'MovieList',
        Components: {
            MovieService
        },
        props: {
            movies: []

        },
        data() {
            return {
                movie: {
                    name: "",
                    description: "",
                    releaseDate: "",
                }
            }
        },


        computed: {

        },
        methods: {
            retrieveMovies() {
                MovieService.getMovies().then(response => {
                    this.movies = response.data
                });
                // MovieService.getMovies().then(response => {
                // this.$store.commit("SET_MOVIES", response.data);

                // }
                //)
            },
            editMovie() {
                // MovieService.updateMovie(this.movie.id, this.movie).then(response => {
                var table = document.querySelector("table");
                table.classList.add("is-hidden");
                // });
            },
            addMovieShowForm() {
                var table = document.querySelector("table");
                table.classList.add("is-hidden");
                var form = document.getElementById("formContainer");
                form.classList.remove("is-hidden");
               
            },
            addMovie() {
                try {
                    MovieService.addMovie(this.movie).then(response => {
                        if (response.status === 200) {
                            alert("Movie successfully added");
                            this.movie = {};
                            this.retrieveMovies();
                            var table = document.querySelector("table");
                            table.classList.remove("is-hidden");
                        }
                    });
                    } catch(e) {
                        alert(e)
                        
                    }
               
            },
            copyMovie(id, movie) {
                MovieService.copyMovie(id, movie).then(response => {
                    if (response.status === 200) {
                        alert("Movie successfully copied");
                        var table = document.querySelector("table");
                        table.classList.add("is-hidden");
                    }
                });
            },


            deleteMovie(id) {
                if (confirm("Are you sure you want to delete this Movie? This action cannot be undone.")) {
                    MovieService.delete(id).then(response => {
                        if (response.status === 200) {
                            alert("Movie successfully deleted");
                            this.retrieveMovies();
                        }
                    });
                }
            },

        },

        created() {
            this.retrieveMovies();
        },

    };


</script>

